}
.footer{
    color: white;
    background-color: #98a0a8;
    align-items: center;  
    justify-content: center;
    flex-wrap: wrap;
    flex-shrink: 0;
    width: 100%;
}

.footer__img{
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
