body {
  margin: 0;
  font-family: 'Muli', sans-serif;
  background: linear-gradient(#006994,#98a0a8);
}
  
* {
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6{
  font-family: 'Fjalla One', sans-serif; 
  font-weight: normal;
  margin: 0px;
}

ul{
  margin: 0px;
}

h1 {
  margin: 0px;
  font-size: 50px;
  line-height: 1,5px; /*espaciado de línea*/
  letter-spacing: -0.2px;  /*espaciado de texto*/
  /*text-transform: uppercase; /*transformar texto texto*/
  /*text-decoration: dashed;  /*decorar de texto*/
}

h2{
  color: white;
  text-transform: uppercase;
  padding: 10px;
  font-family: 'Source Sans Pro',sans-serif;
  font-weight: bold;    
}

figure{
  margin: 0;
}
.home {
    background-image: linear-gradient(#006994,#98a0a8);
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: calc(100vh - 200px); /* El ancho será igual al tamaño de todo el height menos 200px (100px del header + 100px del footer) */
  }
.hero{
    padding:20px;
    background-size: 100% 500px;
    color: white;
    border-top: 2px solid rgba(0,105,148,0.5);
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;
    gap: 10px;
    grid-auto-rows: minmax(100px, auto);
    flex-shrink: 0;
    background-color:#006994;
}

.hero__text {
    width: 530px;
	margin: auto;
	display: table;
    align-self: center;
    justify-self: center;
}

.hero__text-name{ 
	position: relative; 
	float: left;
	background: #006994;
	color: #fff;
	font-size: 2.5em;
    white-space: nowrap;
}

.hero__text-name span {
    position:absolute;
    right:0;
    width:0;
    background: #006994;
    border-left: 2px solid transparent;
    animation: escribir 3s steps(20) 2s 1 normal both,
    blinkTextCursor 500ms steps(20) 1s 8 normal;
}

.hero__text-front{ 
    position: relative; 
    float: left;
    background: #006994;
    color: #fff;
    font-size: 2.5em;
    padding:0px;
}
.hero__text-front span {
    position:absolute;
    right:0;
    width:0;
    background: #006994;
    border-left: 2px solid transparent;
    animation: escribir 4s steps(20) 5s 1 normal both,
    blinkTextCursor 500ms steps(20) 5s 8 normal;
}

.hero__text-ing{ 
    position: relative; 
    float: left;
    background: #006994;
    color: #fff;
    font-size: 2.5em;
    padding:0px;
    white-space: nowrap;
}
.hero__text-ing span {
    position:absolute;
    right:0;
    width:0;
    background: #006994;
    border-left: 2px solid transparent;
    animation: escribir 4s steps(28) 9s normal both,
    blinkTextCursor 500ms steps(28) 9s infinite normal;
}

@keyframes escribir {
    from { width: 100% }
    to { width:0 }
}

@keyframes blinkTextCursor {
    from {
          border-left-color: rgba(255, 255, 255, 0.75);
    }
    to {
          border-left-color: transparent;
    }
}

.hero h1 strong{
    color: #FF6347;
}

.hero__figure{
    width: 280px;
    align-self: center;
    justify-self: center;
}

.hero img{
    margin:20px;
    width: 280px;
    border: 2px solid none;
    background-color: rgba(255,255,255,0.1) ;
    border-radius: 20px;
}

@media only screen and (max-width: 950px)
{
    .hero{
        width: 100%;
        padding:20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .hero__text {
        width: 320px;
    }
    .hero__text-front{ 
        font-size: 1.5em;
    }
    .hero__text-name{ 
        font-size: 1.5em;
    }
    .hero__text-ing{ 
        font-size: 1.5em;
    }
    .hero__figure{
        width: 200px;
    }

    .hero img{
        width: 200px;
        border: 2px solid none;
        background-color: rgba(255,255,255,0.1) ;
        border-radius: 20px;
   }
}
.portfolio{
    margin: 0px;
