}
.certificate-item {
    border-radius: 40px;
    overflow: hidden;
    position: relative;
    display: inline-block;
    width: 250px;
    height: 200px;
    margin-right: 10px;
    font-size: 15px;
    cursor: pointer;
    transition: 450ms all;
    background: rgba(0,0,0,0.1);
    padding: 15px;
    transform-origin: center left;
  }
  
.certificate-item:hover ~ .certificate-item {
    transform: translate3d(100px, 0, 0);
  }

.certificate-item__img{
  width: 70%;
  height: 20%;
  margin: 0;
  padding-bottom:10px;
}

.certificate-item__article a,h3,h5{
  display: flex;
  justify-content:space-evenly;
}

.certificate-item__article div{
  display: flex;
  justify-content:space-evenly;
}

.certificate-item__url a{
  height: 30px;
  width:80px;
  border: 2px solid none;
  background-color: rgba(255,255,255,0.05);
  border-radius: 10px;
  display:flex;
  align-items: center;
  text-decoration: none;
  background-color: white;
  color: black;
}

.certificate-item__url a:hover{
  background-color: rgba(255,255,255,0.25);
  color: white;
}

@media only screen and (max-width: 640px)
{
  .certificate-item {
    border-radius: 40px;
    overflow: hidden;
    width: 250px;
    height: 200px;
    margin-right: 10px;
    font-size: 15px;
    cursor: pointer;
    transition: none;
    transform-origin: none;
  }
  .certificate-item:hover ~ .certificate-item {
    transform: none;
  }
}
.categories{
  display: flex;
  justify-items: right;
  flex-wrap: wrap;
  flex-shrink: 0;
  align-items: center;
  padding: 0px 30px 0px 30px;
  background: linear-gradient(#006994,#98a0a8);
}

.isSearch{
  background: rgba(255,255,255,0.2);
}
.isCertificate{
  background: #98a0a8;
}
.isContact{
  background:  rgba(255,255,255,0.25) ;
  border-top: 2px solid rgba(0,0,0,0.15);
}
.categories__title {
    color: white;
    width: 100%;

}

@media only screen and (max-width: 640px)
{
  .categories{
    padding:0px;
    display: flex;
    flex-direction: column;
  }
}
.certificate {
    overflow: scroll;
    width: 100%;
    position: relative;
    margin: 0;
  }
  
.certificate__container {
  transition: 450ms transform;
  font-size: 0;
  white-space: nowrap;
  margin: 20px 0px;
  padding:10px 10px 30px 10px;
}

.certificate__container:hover .certificate-item {
  opacity: 0.3;
}

.certificate__container:hover .certificate-item:hover {
  transform: scale(1.2);
  opacity: 1;
}

.certificate::-webkit-scrollbar{
  display: none;
}

@media only screen and (max-width: 640px)
{
  .certificate__container:hover .certificate-item:hover {
    transform: scale(1);
    opacity: 1;
  }
}
.contact{
